.container{
    width: 600px; 
    margin: 0 auto; 

}

.card{
    margin: 0 auto;
}
.bg-light {
  background-color: #6893A1!important;
}

.navbar-light .navbar-brand,
nav a {
  color: #650D1B
}

nav i {
  vertical-align: middle;
  padding-right: 5px
}

.nav li {
  margin-right: 25px
}

.userName {
  text-transform: capitalize;
}

nav {
  z-index: 9999;
}


.page-footer,
.footer-copyright {
  background-color: #6893A1;
}

.page-footer {
  padding: 10px 0;
}

#footerText a,
#footerText {
  color: #650D1B;
}

#footerText a {
  padding-left: 10px;
}

.footerSocial i {
  font-size: 20px;
}

#footerText {
  line-height: 17px;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

